body {
  overflow: hidden;
  margin: 0;
  height: 100vh;
}

/* Global */
/* * {
  border: 1px solid red;
} */

* {
  font-family: Avenir, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
    Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji,
    Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
}

/* Chats Page */

.chats-page {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
}

.nav-bar {
  width: 68px;
  height: 100%;
  position: absolute;
  /* background-color: #002766; */
  background-color: #074c94;
}

.logo-tab {
  position: relative;
  height: 66px;
  width: 66px;
  font-size: 32px;
  font-weight: 700;
  color: white;
}

.logo {
  width: 32px;
  padding: 16px;
  padding-bottom: 100%;
}

.inbox-tab {
  height: 60px;
}

.inbox-tab:hover {
  width: 100%;
  background-color: white;
}

.graph-tab > img {
  padding: 16px;
  width: 32px;
}

.logout-tab {
  position: absolute;
  bottom: -5px;
  /* height: 68px; */
  width: 66px;
  color: white;
  cursor: pointer;
}

.profile-picture {
  border-radius: 50%;
  width: 32px;
  padding: 16px;
  padding-top: 22px;
}

.logged-in {
  font-size: 20px;
  position: absolute;
  bottom: 16px;
  right: 20px;
  color: #449d44;
}

::-webkit-scrollbar {
  width: 0; /* Remove scrollbar space */
  background: transparent; /* Optional: just make scrollbar invisible */
}

/* Login Page */

#login-page {
  background-image: linear-gradient(#40a9ff, #096dd9);
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
}

#login-card {
  position: relative;
  top: calc(50vh - 144px);
  left: calc(50vw - 210px);
  padding-top: 36px;
  padding-bottom: 66px;
  width: 420px;
  text-align: center;
  background-color: white;
  border-radius: 22px;
}

.login-button {
  cursor: pointer;
  color: white;
  padding: 12px;
  border-radius: 8px;
  display: inline-block;
}

.facebook {
  background-color: #3b5998;
}

#loading-container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  background-color: rgb(255, 255, 255, 0.22);
}

#loading-icon {
  font-size: 66px;
  position: relative;
  left: calc(50vw - 33px);
  top: calc(50vh - 22px);
}
